<template>
  <div class="content">
    <section class="top">
      <div class="title">- 今日概括 -</div>
      <div class="topLine">
        <div class="line">
          <van-icon :name="this.getRespone.mechanicJson.orderIco" size="25" color="#6CAFF5" />
          <div class="rightLine">
            <p>{{this.getRespone.mechanicJson.order}}</p>
            <div>
              今日预约
              <img src="@/assets/rise.png" />
            </div>
          </div>
        </div>
        <div class="line">
          <van-icon :name="this.getRespone.mechanicJson.dutyIco" size="25" color="#5ED298" />
          <div class="rightLine">
            <p>{{this.getRespone.mechanicJson.duty}}</p>
            <div>
              今日在岗
              <img src="@/assets/rise.png" />
            </div>
          </div>
        </div>
        <div class="line">
          <van-icon :name="this.getRespone.mechanicJson.leaveIco" size="25" color="#F2795B" />
          <div class="rightLine">
            <p>{{this.getRespone.mechanicJson.leave}}</p>
            <div>
              今日请假
              <img src="@/assets/drop.png" />
            </div>
          </div>
        </div>
      </div>
    </section>
    <section class="inline">
      <div class="lineContent">
        <van-icon name="warning" color="#fff" size="50" />
        <div class="right">
          <p class="title">库存告急</p>
          <p class="prompt">百事可乐（550ml）库存仅剩5听，请尽快补仓！</p>
        </div>
      </div>
    </section>
    <section class="bottom">
      <mechanicItem
        :data="data"
        :isAll="isAll"
        :state="state"
        @onclick="$router.push({path: '/ondutyMechanic'})"
        :titleShow="true"
      />
      <mechanicItem
        :data="mechanicJson"
        :isAll="isAll"
        :state="states"
        @onclick="moreDetail"
        @clickDetail="mechaniDetail"
        :titleShow="false"
      />
    </section>
    <footerBottom :tabbars="this.getRespone.bossTabbars" />
  </div>
</template>

<script>
import mechanicItem from "@/components/mechanicItem";
export default {
  data() {
    return {
      mechanicJson: [],
      data: [],
      isAll: true,
      state: 2,
      states: 3
    };
  },
  mounted() {
    var mechaniJsons =  this.getRespone.mechanicJson;
    this.mechanicJson = JSON.parse(JSON.stringify(mechaniJsons));
    this.data = JSON.parse(JSON.stringify(mechaniJsons))
    this.data.item = this.data.item.slice(0, 7);
    this.changeJson();
    
  },
  methods: {
    moreDetail() {
      this.$router.push({
        path: "/restMechanic",
        query: { state: this.states }
      });
    },
    mechaniDetail() {
      console.log("22");
    },
    changeJson() {
      var mechanicData = [];
      this.mechanicJson.item.forEach(element => {
        if (element.state == 3) {
          mechanicData.push(element);
        }
      });
      this.mechanicJson.item = mechanicData;
      this.mechanicJson = this.mechanicJson;
      console.log(JSON.stringify(this.data))
    }
  },
  components: {
    mechanicItem
  }
};
</script>

<style lang="scss" scoped>
.content {
  @include wh(100%, calc(100% - 50px));
  overflow: scroll;
}
.title {
  @include sc(0.95rem, $fColor);
  font-weight: bold;
}
.top {
  @include fc(center);
  padding: 0.8rem 0.4rem;
  .topLine {
    @include fr(space-around);
    @include wh(100%, auto);
    .line {
      @include fr(center);
      margin: 0.8rem 0.5rem;
      .rightLine {
        @include fc(flex-start);
        @include sc(0.7rem, $Tline);
        img {
          @include wh(0.8rem, 0.8rem);
          margin-right: 0.4rem;
        }
        div {
          @include fr(center);
        }
        p {
          @include sc(0.95rem, $line);
          @include wh(100%, auto);
          font-weight: bold;
          text-align: left;
          margin-left: 0.3rem;
        }
      }
    }
  }
}
.lineContent {
  @include fr(flex-start);
  @include sc(0.8rem, $fc);
  padding: 1.5rem 0.8rem;
  background-color: #f96262;
  background-image: linear-gradient(
    90deg,
    #f96262 0%,
    #fb7c66 50%,
    #ff986b 100%
  );
  margin-bottom: 1.2rem;
  .right {
    margin-left: 1rem;
    @include wh(100%, auto);
    text-align: left;
    p:first-child {
      @include sc(0.95rem, $fc);
      margin-bottom: 0.5rem;
    }
  }
}
</style>