<template>
  <section class="bottom">
    <div class="title" v-show="titleShow">- 技师状态 -</div>
    <van-divider>
      <div class="dividerVan">
        {{state !== 3 ? '在岗'+ data.duty +'人':'不在岗'+ data.leave +'人'}}
        <van-icon name="arrow-down" color="#fff" size="15" />
      </div>
    </van-divider>
    <div class="bottom_content">
      <div
        class="bottomLine"
        v-for="(item,index) of data.item"
        :key="index"
        
        @click="clickDetail(item.id)"
      >
        <div class="head">
          <div
            class="headDiv"
            :style="{'color':item.state == 1 ? '#2087F1':item.state == 2 ? '#0DC180':'#acacad','background':item.state == 1 ? '#EAF4FE':item.state == 2 ? '#E6F9F3':'#f4f4f4'}"
          >{{item.name.substring(0,1)}}</div>
          <van-icon
            class="vanIco"
            :name="item.state == 1 ? 'underway':item.state == 2 ? 'checked':'warning'"
            :color="item.state == 1 ? '#2087F1':item.state == 2 ? '#5ED298':'#f4f4f4'"
          />
        </div>
        <p class="num">{{item.num}}</p>
        <van-rate v-model="item.level" allow-half void-icon="star" void-color="#eee" size="12" />
        <p>{{item.name}}</p>
      </div>
      <div class="moreDiv" v-show="isAll">
        <div class="more" @click="onclick">查看更多</div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  methods: {
    onclick() {
      this.$emit("onclick");
    },
    clickDetail(id) {
      this.$router.push({
        path: "/mechanicDetail",
        query: { id: id }
      });
    }
  },
  mounted() {
    // console.log(this.data);
  },
  props: ["data", "state", "isAll", "titleShow"]
};
</script>

<style lang="scss" scoped>
.title {
  @include sc(0.95rem, $fColor);
  font-weight: bold;
}
.bottom {
  // padding: 1rem 0.5rem 0rem 0.5rem;
  background-color: $fc;
  .dividerVan {
    @include sc(0.7rem, $fc);
    @include fr(center);
    padding: 0rem 0.4rem;
    background-color: $DC;
    border-radius: 5rem;
  }
  .bottom_content {
    @include grid(4, auto);
    align-items: center;
    justify-content: center;
    .bottomLine {
      @include fc(center);
      padding: 0.3rem 0rem;
      .head {
        @include fr(center);
        .vanIco {
          margin-left: -0.8rem;
          margin-top: 1.2rem;
          background-color: $fc;
          border-radius: 5rem;
          padding: 0.08rem;
        }
        .headDiv {
          @include wh(2.8rem, 2.8rem);
          @include sc(1rem, $bfont);
          @include fc(center);
          font-weight: bold;
          border-radius: 5rem;
        }
        .warning {
          background: #f4f4f4;
          color: #acacad;
        }
      }

      .num {
        @include sc(0.95rem, $fColor);
        font-weight: bold;
        margin-bottom: 0.3rem;
      }
      p:last-child {
        @include sc(0.8rem, $fColor);
      }
    }
    .moreDiv {
      @include wh(100%, auto);
      .more {
        @include wh(3.5rem, 3.5rem);
        @include sc(0.5rem, $grayfont);
        @include fc(center);
        font-weight: bold;
        border-radius: 5rem;
        background: $Tgray;
        margin: 0.8rem auto;
      }
    }
  }
}
</style>